import { h, Component } from 'preact';
import { observer } from 'mobx-preact';
import util from 'preact-util';

import wutil from '../../lib/wifet-util';
import LineGraph from '../../components/lineGraph';
import Map from '../../components/map';

const initialState = {};

@observer
class Weather extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadAll() {
		const { windmillStore, weatherAiStore, weatherYrStore } = this.props.stores;
		const { currentWindparkName } = windmillStore;

		const xMin = wutil.getDateDiffTime(-4);
		const xMax = wutil.getDateDiffTime(10);
		const now = Math.floor(new Date().getTime() / 1000);
		this.setState({ now, xMin, xMax });

		await this.loadYrData();
		const forcastYrData = weatherYrStore.getWindSpeeds(currentWindparkName);

		weatherAiStore.generateData(forcastYrData);
	}

	async loadYrData() {
		const { weatherYrStore, windmillStore } = this.props.stores;
		const { currentWindpark } = windmillStore;

		weatherYrStore.updateQueryFilter({
			name: currentWindpark.name,
			altitude: currentWindpark.position.altitude,
			lat: currentWindpark.position.lat,
			lon: currentWindpark.position.lon,
		});
		return await weatherYrStore.load();
	}

	setWindpark = (e) => {
		const { windmillStore } = this.props.stores;
		const windpark = e.target.dataset.windpark;
		windmillStore.setCurrentWindparkName(windpark);
		this.loadAll();
	}

	componentDidMount() {
		console.log('componentDidMount');
		this.loadAll();
	}

	render() {
		const { now, xMin, xMax } = this.state;
		const { appState, weatherYrStore, weatherAiStore, windmillStore } = this.props.stores;
		const { darkmode } = appState;
		const { windparks, currentWindparkName, currentWindpark } = windmillStore;
		const windspeeds = weatherYrStore.getWindSpeeds(currentWindparkName);
		const { forcasts: weatherAiData } = weatherAiStore;

		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-4'>
						<h1><i class='fas fa-cloud-sun-rain' /> Weather</h1>
					</div>
					<div class='col-8 pt-2'>
						<ul class='nav nav-pills float-right'>
						{windparks && windparks.map(wp => (
							<li class='nav-item'>
								<button
									class={`btn btn-link nav-link ${wp.name === currentWindparkName ? 'active' : ''}`}
									onClick={this.setWindpark}
									data-windpark={wp.name}
								>{wp.name}</button>
							</li>
						))}
						</ul>
					</div>
					<div class='col-12'>
						<LineGraph
							stores={this.props.stores}
							width={1200}
							height={500}
							showXTicks={1}
							xTicksVal={val => util.isoDate(val)}
							xMin={xMin}
							xMax={xMax}
							paddingTop={30}

							// dataLeft={[windspeeds, aiData, forcastStormData, forcastYrData]}
							dataLeft={[windspeeds, weatherAiData]}
							legendLeft={['Wind forcast Yr', 'Wind AI prediction']}
							yMaxLeft={30}
							yMinLeft={0}
							showYTicksLeft={1}
							yTicksLeft={['30 m/s', '25 m/s', '20 m/s', '15 m/s', '10 m/s', '5 m/s', '0 m/s']}
							unitLeft={['m/s', 'm/s']}

							marker={now}
						/>
					</div>
				</div>
				<div class='row pt-4'>
					<div class='col-12' style='height: 45vh;'>
						<Map stores={this.props.stores}
							darkmode={darkmode}
							center={[currentWindpark.position.lat, currentWindpark.position.lon]}
							zoom={currentWindpark.zoom - 2}
							waypoints={currentWindpark.windmills}
						/>
					</div>
				</div>
				{/* <xmp>{forcasts.length}</xmp>
				<xmp>{JSON.stringify(windspeeds, null, 4)}</xmp>
				<xmp>{JSON.stringify(forcasts, null, 4)}</xmp> */}
			</div>
		);
	}
}

export default Weather;
